@import "../../styles/rwd";

.banner-comp-v2 {
  min-height: 100vh;
  padding-bottom: 24px;

  &-container {
    min-height: calc(100vh - 146.33px);
    padding-right: 24px;
    padding-left: 24px;
  }

  @include mobile {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}

.banner-comp-v2-container {
  .image-container {
    background: unset;
    background-size: unset;

    img {
      width: 100%;
    }

    @include wide-desktop {
      background: url("../../assets/imgs/banner/bg.png") no-repeat center;
      background-size: 100% 100%;
      img {
        display: none;
      }
    }
  }

  .content-container {
    .logo-container, .greeting-container, .title-container, .button-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .greeting-container, .title-container, .button-container {
      &:after {
        content: "";
        width: 50%;
        height: 1px;
        display: block;
        background-color: #140706;
        margin-top: 24px;
      }
    }

    .logo-container {
      margin-bottom: 36px;

      img {
        width: 150px;
      }
    }

    .greeting-container {
      margin-bottom: 24px;

      .text {
        color: #3DBFC3;
        font-size: 50px;
        text-align: center;
        text-transform: uppercase;
        font-weight: 900;
      }
    }

    .title-container {
      padding-bottom: 24px;

      .Typewriter {
        text-align: center;
        color: #EB5151;

        span {
          font-size: 50px;
          font-weight: 900;
        }
      }
    }

    .button-container {
      transition: all .3s linear;
      transform: scale(0);

      button {
        background-color: transparent;
        outline: none;
        border: none;
        font-size: 30px;
        font-weight: 900;
        text-transform: uppercase;
        color: #ffffff;
        transition: all .3s linear;

        &:hover {
          letter-spacing: 5px;
        }
      }
    }

    .button-container-active {
      transform: scale(1) !important;
    }

    .description {
      margin-top: 24px;
      transform: scale(0);
      transition: all .3s linear;

      p {
        color: #ffffff;
        font-weight: 600;
        text-align: center;
        font-size: 18px;
      }
    }

    @include mobile {
      .greeting-container .text, .title-container span, .button-container button {
        font-size: 40px !important;
      }
    }
  }
}