@import "../../styles/rwd";

.banner-comp {
  min-height: 100vh;

  &-container {
    min-height: calc(100vh - 146.33px);
    padding: 24px 0 !important;
    @include mobile {
      min-height: calc(100vh - 126.73px);
    }
    @include tablet {
      min-height: calc(100vh - 92.16px);
    }
  }

  @include mobile {
    padding-bottom: 24px;
  }
}

.banner-comp-container .banner-comp {
  &-content {
    z-index: 1;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  &-title h1 {
    color: rgb(255, 255, 255);
    font-size: 65px;
    text-align: center;
    text-transform: uppercase;
    font-family: "Rubik", sans-serif;

    em {
      -webkit-text-stroke: 1px #fff;
      color: transparent;
      font-style: normal;
      font-weight: 900;
    }

    span {
      font-family: "Rubik", sans-serif;
      font-weight: 900;
      color: rgb(73, 220, 189);
    }

    @include mobile {
      font-size: 50px;
    }
  }

  &-title h2 {
    font-size: 22px;
    color: #ffffff;
    text-align: center;
    text-transform: uppercase;
    margin-top: 12px;
    font-family: "Rubik", sans-serif;
    font-weight: 700;
  }
}

.banner-comp-container .mint-connected {
  width: 100%;

  &-container {
    width: 60%;
    @include mobile {
      width: 100%;
    }
  }
}